
.footer {
  padding-top: 30px;
  box-sizing: border-box;
}
.footer__container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.footer__item {
    padding: 30px 10px;
    width: 50%;
}
.footer__item + .footer__item {
      border-left: 2px solid #6b6b6b;
}
.section_calendar {
  height: 100vh;
  box-sizing: border-box;
  padding-top: 60px;
}
.calendar__container {
  max-width: 1000px;
  text-align: center;
}
.contacts-card {
  background: rgba(0, 0, 0, 0.7);
  position: relative;
  width: 350px;
  padding: 40px 55px;
  font-size: 20px;
  z-index: 1;
  font-weight: 200;
}
.contacts-card__li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin-bottom: 50px;
}
.contacts-card__title {
    color: #eab156;
    font-weight: normal;
}
.map {
  position: relative;
  height: 100vh;
  width: 100%;
  padding-bottom: 260px;
  box-sizing: border-box;
}
.map__container {
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    position: relative;
}
.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.7);
  height: 240px;
  z-index: 5;
}
.wrapper {
  position: relative;
}



